// reset & initial settings

*, 
*::after, 
*::before{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    border: 0;
}

a{
    text-decoration: none;
}

ul,
ol,
li{
    list-style: none;
}

img{
    vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6{
    font-weight: inherit;
    font-size: inherit;
}

html,
body{
    height: 100%;
    line-height: 1;
    font-family: 'Montserrat', sans-serif;
}

img{
    user-select: none;
}

/* [class*="__container"]{
    
} */

.wrapper{
    display: flex;
    flex-direction: column;
    min-height: 100%;
    overflow: hidden;
}

main{
    flex: 1 1 auto;
}

.btn__default{
    outline: none;
    border: none;
    border-radius: 5px;
    background-color: rgb(211, 64, 133);
    overflow: hidden;
    cursor: pointer;
    user-select: none;
    position: relative;
    color: white;
    font-size: 16px;

    &:after{
        content: "";
        display: block;
        width: 20px;
        height: 200px;
        margin-left: 50px;
        background: #fff;
        background: linear-gradient(90deg,hsla(0,0%,100%,.1) 10%,hsla(0,0%,100%,.2) 20%,hsla(0,0%,100%,.6));
        left: -40px;
        top: -100px;
        z-index: 1;
        transform: rotate(45deg);
        position: absolute;
        animation: movingFlare 2.5s ease-in-out 0.05s infinite;
    }
}

@keyframes movingFlare {
    0% {
        left: -30px;
        margin-left: 0px;
    }
    30% {
        left: 110%;
        margin-left: 80px;
    }
    100% {
        left: 110%;
        margin-left: 80px;
    }
}

.quiz__wrapp{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 999;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.6);
    font-family: 'Roboto', sans-serif;
    display: none;
    overflow: hidden;

    &.quiz__active{
        display: flex;
    }

    .quiz__close{
        position: absolute;
        top: -18px;
        right: -15px;
        width: 20px;
        height: 20px;
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        &::after{
            content: '';
            width: 20px;
            height: 2px;
            position: absolute;
            background-color: #fff;
            top: 50%;
            left: 50%;
            transform: rotate(135deg);
        }
        &::before{
            content: '';
            width: 20px;
            height: 2px;
            position: absolute;
            background-color: #fff;
            top: 50%;
            left: 50%;
            transform: rotate(-135deg);
        }
    }
}