.quiz{
    width: 1200px;
    height: 650px;
    background-color: rgb(243, 243, 243);
    /* position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); */
    position: relative;
}

.steps__wrapp{
    width: 1200px;
    height: 650px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0 30px;
}

.steps__content{
    height: calc(100% - 104px - 61px);
    display: flex;
    padding: 20px 0;
    flex-direction: column;
    overflow: auto;

    &::-webkit-scrollbar{
        width: 0;
    }
}

.steps__title{
    font-size: 26px;
    font-weight: 400;
    color: #363636;
}

.steps__footer{
    width: 100%;
    height: 104px;
    display: flex;
    justify-content: flex-end;
    align-items: center;

    position: relative;

    &::after{
        content: '';
        position: absolute;
        width: calc(100% + 60px);
        height: 1px;
        background-color: #d9d9d9;
        top: 0;
        left: -30px;
    }

    .btn__prev{
        width: 43px;
        height: 43px;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 0;
        outline: 0;
        border: 1px solid #dbdbdb;
        border-radius: 3px;
        cursor: pointer;
        background-color: transparent;
        margin-right: 15px;

        svg{
            width: 18px;
            height: 18px;
            fill: rgba(0,0,0,.5);
        }

        &:disabled{
            cursor: not-allowed;
        }
        

        &:not(:disabled):hover{
            svg{
                fill: #363636;
                transition: .3s linear;
            }
        }
    }

    .btn__next{
        display: flex;
        align-items: center;
        height: 43px;
        padding: 0 20px;

        &:disabled{
            opacity: .8;
            cursor: not-allowed;
            box-shadow: 0 3px 10px 0 rgba(211, 64, 133, 0.5);
            animation: none;

            &::after{
                display: none;
            }
        }

        svg{
            fill: white;
            margin-left: 5px;
            width: 18px;
            height: 18px;
        }
    }
}

.steps__header{
    width: 100%;
    height: 61px;
    display: flex;
    align-items: center;
    position: relative;

    &::after{
        content: '';
        position: absolute;
        width: calc(100% + 60px);
        height: 1px;
        background-color: #d9d9d9;
        bottom: 0;
        left: -30px;
    }

    p.title{
        font-weight: 400;
        color: rgba(54, 54, 54, 0.7);
        font-size: 14px;
        margin-left: 10px;
    }

    svg{
        width: 20px;
        height: 20px;
        fill: #d34085;
    }
}


#progress_bar{
    position: absolute;
    bottom: 0;
    left: 0;
    height: 104px;
    width: 75%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 30px;

    #curr_step{
        margin-bottom: 10px;
        font-weight: 400;
        color: rgba(54, 54, 54, 0.7);
        font-size: 15px;
        margin-left: 0;

        span{
            color: #d34085;
            font-weight: 700;
        }
    }

    #progress_in{
        width: 100%;
        height: 10px;
        background-color: #d9d9d9;
        border-radius: 10px;

        span{
            display: block;
            height: 100%;
            border-radius: 20px;
            background-image: -webkit-gradient(linear,left top,right top,from((#de1290),to(#f263a6)));
            background-image: linear-gradient(90deg,#de1290,#f263a6);
            -webkit-box-shadow: inset 0 2px 9px hsla(0,0%,100%,.3),inset 0 -2px 6px rgba(0,0,0,.4),0.6px 1.9px 7px 0 rgba(239, 54, 199, 0.5);
            box-shadow: inset 0 2px 9px hsla(0,0%,100%,.3),inset 0 -2px 6px rgba(0,0,0,.4),0.6px 1.9px 7px 0 rgba(239, 54, 199, 0.5);
            position: relative;
            overflow: hidden;
            -webkit-transition: all .3s ease;
            transition: all .3s ease;
        }
    }
}

@media (max-width: 830px){
    #progress_bar{
        width: 50%;
    }
}

#other_steps.last__step__bar{
    #progress_bar{
        width: 62%;
        top: 0;

        #curr_step{
            text-align: center;
        }
    }
}

@media (max-width: 430px){
    .steps__footer{
        height: 154px;
        justify-content: center;
        align-items: flex-end;
        padding-bottom: 20px;
        z-index: 50;
    }

    #progress_bar{
        width: 100%;
        padding-right: 30px;
        height: 154px;
        align-items: center;
        bottom: 20px;
    }
}

@media (max-width: 1280px){
    .quiz{
        width: 100%;
        height: 100vh;
        top: 0;
        left: 0;
        transform: translate(0, 0);
        /* position: fixed; */
    }

    .quiz__wrapp{
        .quiz__close{
            top: 15px;
            right: 20px;
            &::after, &::before{
                background-color: #ADADAD;
            }
        }
    }

    .steps__wrapp{
        width: 100%;
        height: 100vh;
    }
}

#step0, #step1, #step2, #step3, #step4, #step5, #step6, #step7, #step8, #step9, #step10, #other_steps, #last_step {display: none;}

#step0{
    display: block;
}