.prelast__step{
    padding: 0;
    flex-direction: row;
    overflow: hidden;

    .prelast__step__wrapp{
        display: flex;
        align-items: center;
    }

    .steps__content{
        height: 100%;
        padding: 0;
        width: 65%;
        justify-content: center;
        padding: 0 75px;
        padding-left: 30px;
        border-right: 1px solid #d9d9d9;
        overflow: hidden;

        p.header__quiz{
            font-size: 26px;
            font-weight: 400;
            color: #363636;
            text-align: center;
            line-height: 120%;
        }

        p.text__quiz{
            font-weight: 400;
            color: rgba(54, 54, 54, 0.7);
            font-size: 15px;
            margin-top: 15px;
            line-height: 140%;
        }
    }

    .steps__form{
        width: 250px;
    }
}

.select__gift__info{
    align-self: center;
    text-align: center;

    p.text__quiz{
        font-weight: 400;
        color: rgba(54, 54, 54, 0.7);
        font-size: 15px;
        margin-top: 15px;
        line-height: 140%;
    }

    .gift{

        p.gift__item{
            display: none;
        }
    }
}

.select__gift__images{
    margin-top: 30px;

    picture{
        display: none;
    }

    width: 100%;
    height: auto;

    img{
        width: 100%;
        height: 100%;
        object-fit: 100%;
    }
}

