.steps__form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-self: center;
    margin-top: 50px;

    .btn__default{
        width: 100%;
        height: 51px;
        font-weight: 500;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 30px;

        p{
            padding-left: 10px;
        }

        svg{
            width: 24px;
            height: 24px;
            fill: white;
        }
    }
}

.steps__form__input__item{
    margin-top: 30px;
    &:first-child{
        margin-top: 0;
    }

    p.title__quiz{
        color: rgba(54, 54, 54, 0.7);
        font-weight: 700;
        font-size: 12px;
        text-transform: uppercase;
        letter-spacing: 1.2px;
        text-align: center;
    }

    .steps__form__input{
        margin-top: 10px;
        border: 1px solid #d9d9d9;
        border-radius: 5px;
        display: flex;
        align-items: center;
        width: 100%;
        height: 60px;
        padding: 0 15px;
        .input__icon{
            
            svg{
                width: 24px;
                height: 24px;
                fill: #d9d9d9;
            }
        }

        input{
            width: 100%;
            height: 100%;
            background-color: transparent;
            outline: none;
            padding: 0 10px;
            font-size: 16px;
        }
    }

    &.valid__warning{

        .steps__form__input{
            border-color: #d10a34;
        }
        
        .input__valid__warning{
            display: block;
        }
    }

    .input__valid__warning{
        display: none;
        svg{
            width: 24px;
            height: 24px;
            fill: #d10a34;
        }
    }

}

.select__gift{
    display: flex;
    width: 35%;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.select__gift__icon{
    width: 72px;
    height: 72px;
    margin: 0 auto;
    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

@media (max-width: 990px){

    .select__gift{
        justify-content: flex-start;
        align-items: flex-start;
        width: 100%;
        margin-bottom: 0;
        height: 30%;
    }

    #other_steps.last__step__bar{
        #progress_bar{
            position: static;
            width: 100%;
            padding-right: 30px;
        }
    }


    .prelast__step{
        padding: 0;
        .prelast__step__wrapp{
            overflow: auto;
            flex-direction: column;
        }
    
        .steps__content{
            height: 100%;
            width: 100%;
            justify-content: center;
            padding: 75px 30px;
            border-right: 1px solid #d9d9d9;
            text-align: center;
    
            p.title__quiz{
                font-size: 26px;
                font-weight: 400;
                color: #363636;
            }
    
            p.text__quiz{
                font-weight: 400;
                color: rgba(54, 54, 54, 0.7);
                font-size: 15px;
                margin-top: 15px;
                line-height: 140%;
            }
        }
    
        .steps__form{
            width: 100%;
            height: 100%;
        }
    }

    .steps__form{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;

        p.title__quiz{
            font-size: 18px!important;
            font-weight: 400;
            color: #363636;
        }
    }
}

