.step5{
    grid-template-columns: 348px 348px 348px!important;
    justify-content: flex-start!important;
    column-gap: 20px!important;
    row-gap: 15px!important;
    .--default{
        width: 100%;
        border: 1px solid #d9d9d9;
        display: flex;
        border-radius: 5px;
        transition: .15s linear;
        
        &.label__active{
            border-color: rgba(211, 64, 133, 0.5);
            transition: .3s linear;
        }
        
        &:hover{
            box-shadow: 0 1px 3px 0 #d9d9d9;
            transition: .3s linear;
        }
        
        label.quiz__labels{
            width: 100%;
            height: 100%;
            padding: 15px;
            cursor: pointer;
            display: flex;
            align-items: center;
            position: relative;
            
            input{
                position: relative;
                
                &:after{
                    position: absolute;
                    content: '';
                    width: 20px;
                    height: 20px;
                    background-color: #d9d9d9;
                    border-radius: 50%;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%)
                }
                
                &:checked::after{
                    border: 2px solid #d34085;
                    box-shadow: 0 2px 6px 0 #d34085;
                    transition: box-shadow .3s ease;
                }
                
                &:checked::before{
                    position: absolute;
                    content: '';
                    width: 10px;
                    height: 10px;
                    background-color: #d34085;
                    z-index: 2;
                    border-radius: 50%;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    transition: 1s linear;
                    
                }
            }
            
            span{
                color: #363636;
                font-size: 15px;
                margin-left: 15px;
            }
        }

    }

    &.econom{
        .choice__style__item{
            &:nth-child(3),
            &:nth-child(4),
            &:nth-child(5),
            &:nth-child(6){
                display: none;
            }
        }
    }

    &.standart{
        .choice__style__item{
            &:nth-child(1),
            &:nth-child(2),
            &:nth-child(5),
            &:nth-child(6){
                display: none;
            }
        }
    }

    &.premium{
        .choice__style__item{
            &:nth-child(1),
            &:nth-child(2),
            &:nth-child(3),
            &:nth-child(4){
                display: none;
            }
        }
    }
}

@media (max-width: 1160px){
    .step5{
        width: calc(348px + 348px + 30px);
    }
}

@media (max-width: 830px){
    .step5{
        width: 348px;
    }
}

@media (max-width: 513px){
    .loc__option__item {
        width: calc(100%)!important;
    }
    .step5{
        width: 348px;
    }
}

@media (max-width: 411px){
    .loc__option__item {
        width: calc(100%)!important;
    }
    .step5{
        width: 100%;
    }
}