.choice__style__wrapp:not(.step1){
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    column-gap: 20px;
    row-gap: 15px;
    margin-top: 30px;
    .choice__style__item{
        width: calc(50% - 20px);
        border: 1px solid #d9d9d9;
        display: flex;
        border-radius: 5px;
        transition: .15s linear;
        
        &.label__active{
            border-color: rgba(211, 64, 133, 0.5);
            transition: .3s linear;
        }
        
        &:hover{
            box-shadow: 0 1px 3px 0 #d9d9d9;
            transition: .3s linear;
        }
        
        label.quiz__labels{
            width: 100%;
            height: 100%;padding: 15px;
            cursor: pointer;
            display: flex;
            align-items: center;
            position: relative;
            
            input{
                position: relative;
                
                &:after{
                    position: absolute;
                    content: '';
                    width: 20px;
                    height: 20px;
                    background-color: #d9d9d9;
                    border-radius: 50%;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%)
                }
                
                &:checked::after{
                    border: 2px solid #d34085;
                    box-shadow: 0 2px 6px 0 #d34085;
                    transition: box-shadow .3s ease;
                }
                
                &:checked::before{
                    position: absolute;
                    content: '';
                    width: 10px;
                    height: 10px;
                    background-color: #d34085;
                    z-index: 2;
                    border-radius: 50%;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    transition: 1s linear;
                    
                }
            }
            
            span{
                color: #363636;
                font-size: 15px;
                margin-left: 15px;
            }
        }
    }
}

.choice__style__wrapp.step1{
    display: grid;
    grid-template-columns: 348px 348px 348px;
    justify-content: space-between;
    row-gap: 30px;
    column-gap: 0;
    margin-top: 30px;

    .loc__option__item{

        &.label__active{
            border-color: rgba(211, 64, 133, 0.5);
            transition: .3s linear;
        }
    }
    
}

@media (max-width: 1160px){
    .choice__style__wrapp.step1{
        display: flex;
        flex-wrap: wrap;
        grid-template-columns: none;
        justify-content: center;
        column-gap: 30px;
    }
}

@media (max-width: 990px){
    .steps__title{
        margin-top: 20px;
    }
    .choice__style__wrapp.step1{
        justify-content: flex-start;
    }

    .choice__style__wrapp{
        margin-bottom: 20px;
    }
}

@media (max-width: 830px){
    .choice__style__wrapp.step1{
        flex-direction: column;
    }
}