.first__step{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    overflow: auto;
}

.first__step__content{
    width: 500px;
    padding: 0 30px;
    p.header__quiz{
        font-size: 26px;
        font-weight: 700;
        color: #363636;
        position: relative;
        line-height: 140%;

        &::after{
            content: '';
            position: absolute;
            width: 40px;
            height: 3px;
            background-color: #363636;
            top: -20px;
            left: 0;
        }
    }

    p.text__quiz{
        font-weight: 400;
        margin-top: 15px;
        color: rgba(54, 54, 54, 0.7);
        font-size: 16px;
    }

    .btn__default{
        width: 155px;
        height: 55px;
        font-weight: 500;
        margin-top: 30px;
    }

    p.title{
        font-size: 20px;
        font-weight: 500;
        color: #363636;
        position: relative;
        padding-top: 40px;
    }
}

.first__step__images{
    width: 700px;
    height: 650px;

    picture{

        source, img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}

.benefits__item{
    padding: 15px 10px;
    border-radius: 5px;
    border: 1px solid rgba(54, 54, 54, 0.7);
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 15px;
    height: auto;

    p.benefits__item__text{
        font-size: 18px;
        font-weight: 500;
        color: #363636;
        padding-right: 15px;
    }

    .benefits__item__images{
        width: 103px;
        height: 72px;
        /* background-color: #fff; */

        img{
            width: 100%;
            height: 100%;
            object-fit: 100%;
        }
    }
}

@media (max-width: 988px){
    .first__step__content{
        p.title{
            text-align: center;
        
            br{
                display: none;
            }
        }
    }
}

@media (max-width: 1280px){
    .first__step__images{
        width: 55%;
        height: 100vh;
    }

    .first__step__content{
        width: 45%;
    }
    
}

@media (max-width: 484px){
    .benefits__item{

        flex-direction: column;
    
        p.benefits__item__text{
            font-size: 16px;
            text-align: center;
            br{
                display: none;
            }
        }
    
        .benefits__item__images{
            margin-top: 10px;
        }
    }
}

@media (max-width: 990px){
    .first__step__images{
        width: 100%;
        height: 400px;
        order: 1;

        picture{

            source, img{
                width: 100%;
                height: 100%;
                object-fit: 100%;
            }
        }
    }

    .first__step__content{
        width: auto;
        margin-top: 60px;
        order: 2;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 0 30px;

        p.header__quiz{
            width: 500px;
            text-align: center;
    
            &::after{
                left: 50%;
                transform: translateX(-50%);
            }
        }
    
        p.text__quiz{
            text-align: center;
        }
    }

    .first__step{
        justify-content: flex-start;
        flex-direction: column;
        width: 100%;
        height: 100vh;
        overflow: auto;
        padding-bottom: 50px;
    }
    
    
}

@media (max-width: 768px){
    .first__step__content{
        width: 100%;

        p.header__quiz{
            width: 100%;
        }

        .btn__default{
            width: 100%;
        }
    }
}

@media (max-width: 604px){
    .first__step__images{
        height: auto;
    }
}