/* stylelint-disable */
*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
}

a {
  text-decoration: none;
}

ul,
ol,
li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

html,
body {
  height: 100%;
  line-height: 1;
  font-family: "Montserrat", sans-serif;
}

img {
  user-select: none;
}

/* [class*="__container"]{

} */
.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  overflow: hidden;
}

main {
  flex: 1 1 auto;
}

.btn__default {
  outline: none;
  border: none;
  border-radius: 5px;
  background-color: rgb(211, 64, 133);
  overflow: hidden;
  cursor: pointer;
  user-select: none;
  position: relative;
  color: white;
  font-size: 16px;
}
.btn__default:after {
  content: "";
  display: block;
  width: 20px;
  height: 200px;
  margin-left: 50px;
  background: #fff;
  background: linear-gradient(90deg, hsla(0deg, 0%, 100%, 0.1) 10%, hsla(0deg, 0%, 100%, 0.2) 20%, hsla(0deg, 0%, 100%, 0.6));
  left: -40px;
  top: -100px;
  z-index: 1;
  transform: rotate(45deg);
  position: absolute;
  animation: movingFlare 2.5s ease-in-out 0.05s infinite;
}

@keyframes movingFlare {
  0% {
    left: -30px;
    margin-left: 0px;
  }
  30% {
    left: 110%;
    margin-left: 80px;
  }
  100% {
    left: 110%;
    margin-left: 80px;
  }
}
.quiz__wrapp {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 999;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  font-family: "Roboto", sans-serif;
  display: none;
  overflow: hidden;
}
.quiz__wrapp.quiz__active {
  display: flex;
}
.quiz__wrapp .quiz__close {
  position: absolute;
  top: -18px;
  right: -15px;
  width: 20px;
  height: 20px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
.quiz__wrapp .quiz__close::after {
  content: "";
  width: 20px;
  height: 2px;
  position: absolute;
  background-color: #fff;
  top: 50%;
  left: 50%;
  transform: rotate(135deg);
}
.quiz__wrapp .quiz__close::before {
  content: "";
  width: 20px;
  height: 2px;
  position: absolute;
  background-color: #fff;
  top: 50%;
  left: 50%;
  transform: rotate(-135deg);
}

.header {
  width: 100%;
  height: 113px;
  background: url("../img/header/header__bg.png") no-repeat, #000;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 40px;
}

.header__logo {
  display: flex;
  align-items: center;
}
.header__logo p {
  margin-left: 20px;
  color: white;
  font-weight: 500;
  font-size: 14px;
}

ul.header__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 500px;
  margin-left: 10%;
}
ul.header__nav li.header__nav__item a {
  color: white;
  font-weight: 500;
  font-size: 14px;
  /* &:hover{
      color: rgb(187, 187, 187);
      transition: .3s color ease;
  } */
}

.header__callback {
  display: flex;
  flex-direction: column;
  text-align: right;
  position: relative;
}
.header__callback::before {
  content: url("../img/header/call.png");
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: -80px;
}
.header__callback a.header__callback__number__mob {
  display: none;
}
.header__callback a.header__callback__number {
  font-weight: 700;
  color: white;
  font-size: 24px;
}
.header__callback a.header__callback__order {
  font-weight: 500;
  color: white;
  font-size: 12px;
  margin-top: 5px;
}
.header__callback a.header__callback__order:hover {
  color: black;
  transition: 0.3s color ease;
}

.header__burger {
  display: none;
}

.header__burger {
  cursor: pointer;
  width: 22px;
  height: 16px;
  position: relative;
  justify-content: center;
  align-items: center;
}
.header__burger span {
  width: 100%;
  height: 2px;
  border-radius: 3px;
  background-color: #fff;
  position: relative;
}
.header__burger span::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  border-radius: 3px;
  background-color: #fff;
  bottom: -7px;
  transition: 0.3s ease;
}
.header__burger span::before {
  content: "";
  position: absolute;
  top: -7px;
  left: 0;
  width: 100%;
  height: 2px;
  border-radius: 3px;
  background-color: #fff;
  transition: 0.3s ease;
}

/*    Mobile    */
@media (max-width: 990px) {
  .header {
    height: 70px;
    background-image: none;
    padding: 0 20px;
  }
  .header__burger {
    display: flex;
  }
  .header__menu {
    order: 3;
  }
  .header__menu .header__nav {
    display: none;
  }
  .header__menu.menu__active .header__burger span {
    background-color: transparent;
  }
  .header__menu.menu__active .header__burger span::before {
    top: 0;
    transform: rotate(135deg);
  }
  .header__menu.menu__active .header__burger span::after {
    bottom: 0;
    transform: rotate(-135deg);
  }
  .header__menu.menu__active .header__nav {
    display: flex;
    position: absolute;
    top: 70px;
    left: 0;
    margin: 0;
    background-color: black;
    width: 100%;
    height: calc(100vh - 70px);
    z-index: 25;
    flex-direction: column;
    justify-content: flex-start;
  }
  .header__menu.menu__active .header__nav .header__nav__item {
    margin-top: 10%;
  }
  .header__logo {
    order: 2;
  }
  .header__logo img {
    width: 124px;
    height: 46px;
  }
  .header__logo p {
    display: none;
  }
  .header__callback {
    order: 1;
  }
  .header__callback {
    display: flex;
    flex-direction: column;
    text-align: right;
    position: relative;
  }
  .header__callback::before {
    display: none;
  }
  .header__callback a.header__callback__number__mob {
    display: block;
  }
  .header__callback a.header__callback__number {
    display: none;
  }
  .header__callback a.header__callback__order {
    display: none;
  }
}
