.quiz {
  width: 1200px;
  height: 650px;
  background-color: rgb(243, 243, 243);
  /* position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); */
  position: relative;
}

.steps__wrapp {
  width: 1200px;
  height: 650px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0 30px;
}

.steps__content {
  height: calc(100% - 104px - 61px);
  display: flex;
  padding: 20px 0;
  flex-direction: column;
  overflow: auto;
}
.steps__content::-webkit-scrollbar {
  width: 0;
}

.steps__title {
  font-size: 26px;
  font-weight: 400;
  color: #363636;
}

.steps__footer {
  width: 100%;
  height: 104px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: relative;
}
.steps__footer::after {
  content: "";
  position: absolute;
  width: calc(100% + 60px);
  height: 1px;
  background-color: #d9d9d9;
  top: 0;
  left: -30px;
}
.steps__footer .btn__prev {
  width: 43px;
  height: 43px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0;
  outline: 0;
  border: 1px solid #dbdbdb;
  border-radius: 3px;
  cursor: pointer;
  background-color: transparent;
  margin-right: 15px;
}
.steps__footer .btn__prev svg {
  width: 18px;
  height: 18px;
  fill: rgba(0, 0, 0, 0.5);
}
.steps__footer .btn__prev:disabled {
  cursor: not-allowed;
}
.steps__footer .btn__prev:not(:disabled):hover svg {
  fill: #363636;
  transition: 0.3s linear;
}
.steps__footer .btn__next {
  display: flex;
  align-items: center;
  height: 43px;
  padding: 0 20px;
}
.steps__footer .btn__next:disabled {
  opacity: 0.8;
  cursor: not-allowed;
  box-shadow: 0 3px 10px 0 rgba(211, 64, 133, 0.5);
  animation: none;
}
.steps__footer .btn__next:disabled::after {
  display: none;
}
.steps__footer .btn__next svg {
  fill: white;
  margin-left: 5px;
  width: 18px;
  height: 18px;
}

.steps__header {
  width: 100%;
  height: 61px;
  display: flex;
  align-items: center;
  position: relative;
}
.steps__header::after {
  content: "";
  position: absolute;
  width: calc(100% + 60px);
  height: 1px;
  background-color: #d9d9d9;
  bottom: 0;
  left: -30px;
}
.steps__header p.title {
  font-weight: 400;
  color: rgba(54, 54, 54, 0.7);
  font-size: 14px;
  margin-left: 10px;
}
.steps__header svg {
  width: 20px;
  height: 20px;
  fill: #d34085;
}

#progress_bar {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 104px;
  width: 75%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 30px;
}
#progress_bar #curr_step {
  margin-bottom: 10px;
  font-weight: 400;
  color: rgba(54, 54, 54, 0.7);
  font-size: 15px;
  margin-left: 0;
}
#progress_bar #curr_step span {
  color: #d34085;
  font-weight: 700;
}
#progress_bar #progress_in {
  width: 100%;
  height: 10px;
  background-color: #d9d9d9;
  border-radius: 10px;
}
#progress_bar #progress_in span {
  display: block;
  height: 100%;
  border-radius: 20px;
  background-image: -webkit-gradient(linear, left top, right top, from(#de1290, to(#f263a6)));
  background-image: linear-gradient(90deg, #de1290, #f263a6);
  -webkit-box-shadow: inset 0 2px 9px hsla(0deg, 0%, 100%, 0.3), inset 0 -2px 6px rgba(0, 0, 0, 0.4), 0.6px 1.9px 7px 0 rgba(239, 54, 199, 0.5);
  box-shadow: inset 0 2px 9px hsla(0deg, 0%, 100%, 0.3), inset 0 -2px 6px rgba(0, 0, 0, 0.4), 0.6px 1.9px 7px 0 rgba(239, 54, 199, 0.5);
  position: relative;
  overflow: hidden;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

@media (max-width: 830px) {
  #progress_bar {
    width: 50%;
  }
}
#other_steps.last__step__bar #progress_bar {
  width: 62%;
  top: 0;
}
#other_steps.last__step__bar #progress_bar #curr_step {
  text-align: center;
}

